@keyframes rotate {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  .rotate {
    animation: rotate 10s linear infinite;
  }
  
.circular-progress-bar {
    display: block;
    margin: auto;
    max-width: 100px;
    max-height: 100px;
  }
  .circular-progress-bar {
  transform: scale(1);
  filter: drop-shadow(4px 4px 6px rgba(0, 0, 0, 0.2));
}

.background-circle {
  filter: drop-shadow(2px 2px 4px rgba(0, 0, 0, 0.1));
}

