.leave-component {
  width: 250px;
  background-color: #f5f5f5;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0px;
}

.see-all {
  background-color: #ffffff;
  border: none;
  color: #888;
  font-size: 14px;
  cursor: pointer;
}

.progress-circle {
  position: relative;
  width: 150px;
  height: 100px;
  margin: 0 auto 0px auto;
}

.leave-count {
  position: absolute;
  bottom: -1%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.leave-count h6 {
  font-size: 1.4rem;
  margin: 0;
  /* font-weight: bold; */
}

.leave-count span {
  font-size: 0.9rem;
}

.leave-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.leave-item {
  display: flex;
  align-items: center;
  gap: 10px;
}

.leave-icon {
  font-size: 18px;
}

.leave-date {
  flex-grow: 1;
  color: #333;
}

.leave-type {
  color: #555;
}

.leave-status {
  padding: 2px 8px;
  border-radius: 10px;
  text-transform: capitalize;
}

.popover {
  position: absolute;
  padding: 10px;
  z-index: 1000;
}
